  justify-content: center;
  align-items: center;
}


.box1{
  background: #1d0068;
  border: 3px solid #1d0068;
  width: 50%;
  height: auto;
  padding: 10px;
  border-radius: 8px;
  transition: all 2s;

  position: relative;
 
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;

  animation: animate2 2.5s;
}

.box1:hover{
  transform: rotate3d(1, 1, 1,-16deg);
}

.box1 h3{
  color: #fff !important;
  text-transform: uppercase;
  font-size: 50px;
}

.box1 .icon{
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: #3cc376;
  color: #1d0068;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 600;
  font-size: 40px;
  position: absolute;
  bottom: -24px;
  right: 34px;
}

.box2 .icon{
