  color: #1d0068;
  position: absolute;
  font-size: 24px;
  font-weight: 600;
  left: -22px;
}



.message{
  background: #3cc27d;
  color: #fff;
  width: 400px;
  text-align: center;
  padding: 10px;
  position: fixed !important;
  bottom: 120px;
  left: 44px;
  z-index: 99;
  transition: all 2s;
  animation: message 3s;
}

.message .arrow{
  width: 0;
  height: 0;
  border-left: 20px solid #3cc27d;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  transform: rotate(90deg);

  position: absolute;
  bottom: -16px;

  
}

.message.deactive{
  display: none;
}

.box-important{
  background: #1d0068;
}


